<template>
  <div>
    <el-table
      :row-style="{ background: 'rgba(0, 0, 0, 0)' }"
      :header-row-style="{ background: 'rgba(0, 0, 0, 0)' }"
      :header-cell-style="{ background: 'rgba(0, 0, 0, 0)' }"
      v-loading="loading"
      :data="teams"
      :height="height"
    >
      <el-table-column label="团队名称">
        <template v-slot="scope">
          <div @dblclick="editTeam(scope.$index, scope.row ,$event)">
            <i v-if="editIndex+1 === scope.$index+1 ? false : true " class="ico icon-tuandui"></i>
            <span
              v-if="editIndex+1 === scope.$index+1 ? false : true "
              style="margin-left: 10px"
            >{{ scope.row.name }}</span>
          </div>
          <el-input
            v-if="editIndex+1 === scope.$index+1 ? true : false "
            size="mini"
            prefix-icon="ico icon-tuandui"
            :placeholder="scope.row.name"
            v-model="text"
            :class="prompt? 'prompt': ''"
            @focus="clearDuplicate(scope.row.name)"
            @keyup.enter.native="_updateTeam(scope.$index, scope.row,$event)"
          ></el-input>
        </template>
      </el-table-column>
      <el-table-column label="团队成员">
        <template v-slot="scope">
          <el-popover trigger="hover" placement="top" width="280">
            <p>团队: {{ scope.row.name }}</p>
            <p>成员: {{ scope.row.members | membersPopover }}</p>
            <div slot="reference" class="name-wrapper">
              <el-tag size="medium">{{ scope.row.members | members }}</el-tag>
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column align="right">
        <template v-slot:header>
          <el-popover trigger="hover">
            <p>提示：</p>
            <p>团队中一旦有成员，则禁止编辑或删除团队</p>
            <div slot="reference" class="name-wrapper">
              操作
              <i class="el-icon-question"></i>
            </div>
          </el-popover>
        </template>
        <template v-slot="scope">
          <el-button
            v-if="editIndex+1 !== scope.$index+1 ? true: false "
            size="mini"
            @click="editTeam(scope.$index, scope.row, $event)"
            :disabled="scope.row.members.length? true: false"
          >编辑</el-button>
          <el-button
            v-if="editIndex+1 !== scope.$index+1 ? true: false "
            size="mini"
            type="danger"
            @click="_deleteTeam(scope.$index, scope.row)"
            :disabled="scope.row.members.length ? true: false"
          >删除</el-button>
          <el-button
            v-if="editIndex+1 === scope.$index+1 ? true: false "
            size="mini"
            type="primary"
            @click="_updateTeam(scope.$index, scope.row, $event)"
          >保存</el-button>
          <el-button
            v-if="editIndex+1 === scope.$index+1 ? true: false"
            size="mini"
            @click="cancel(scope.$index, scope.row)"
          >取消</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
import { mapGetters, mapActions } from 'vuex'
import dcopy from 'deep-copy'
import autoHeight from '@/views/mixins/autoHeight'
export default {
  name: 'Table',
  mixins: [autoHeight],
  data () {
    return {
      loading: true,
      subHeight: 400, // mixins/autoHeight 自定义参数
      editIndex: undefined,
      prompt: false, // 编辑输入框错误提示（边框颜色改变）
      name: '',
      duplicate: ''
    }
  },
  computed: {
    ...mapGetters(['teams']),
    text: {
      get () {
        if (this.duplicate) { return this.duplicate } else {
          return this.name
        }
      },
      set (value) {
        this.name = value
      }
    }
  },
  watch: {
    teams: {
      handler (val) {
        if (val.length) {
          this.loading = false
        }
      },
      immediate: true
    }
  },
  filters: {
    members (val) {
      let str = ''
      if (!val.length) {
        str = '~~团中没有成员~~'
        return str
      }

      val.forEach(item => { str += `${item.name}、` })
      return str.substr(0, str.length - 1)
    },
    membersPopover (val) {
      let str = ''
      if (!val.length) {
        str = '~~团中没有成员~~'
        return str
      }
      val.forEach(item => { str += `${item.name}、` })
      return str.substr(0, str.length - 1)
    }
  },
  mounted () {
    this.$nextTick(() => {
      document.addEventListener('click', this.cancel)
    })
  },
  beforeDestroy () {
    document.removeEventListener('click', this.cancel)
  },
  methods: {
    ...mapActions([
      'deleteTeam',
      'updateTeam'
    ]),
    editTeam (index, row, event) {
      event.stopPropagation()
      this.editIndex = index
      this.name = ''
    },
    _deleteTeam (index, row) {
      const _id = { _id: row._id }
      this.deleteTeam({ _id, index })
    },
    async _updateTeam (index, row, event) {
      event.stopPropagation()
      if (this.name === '') {
        this.editIndex = undefined
        return
      }
      if (this.duplicate) return
      const team = dcopy(row)
      team.name = this.name
      try {
        const res = await this['updateTeam']({ team, index })
        if (res.data) {
          this.editIndex = undefined
        }
      } catch (err) {
        this.prompt = true
        this.duplicate = err.message
      }
    },
    clearDuplicate (name) {
      this.duplicate = ''
      this.text = name
      this.prompt = false
    },
    // 点击输入框以外的区域隐藏输入框
    cancel (e) {
      if (e.target && e.target.className === 'el-input__inner') return
      this.editIndex = undefined
      this.duplicate = '' // 重复提示
      this.prompt = false // ⚠️警告色不显示
    }
  }
}
</script>
<style scoped>
/**表头问号图标调整 */
.el-table th div {
  line-height: 33px;
}
.el-table /deep/ .is-leaf {
  padding: 0px !important;
}
/**编辑框宽度 */
.el-input {
  width: 200px;
}
/**编辑框错误提示 */
.prompt /deep/ .el-input__inner {
  border-color: red;
}

/** 表格 透明⬇⬇⬇*/
.el-table {
  background: rgba(0, 0, 0, 0);
}
/** 表格行高 */
.el-table /deep/ .el-popover__reference {
  padding: 0px;
}
/** 表格滚动条 */
.el-table /deep/ .el-table__header-wrapper .gutter {
  background: rgba(0, 0, 0, 0);
}
.el-table /deep/ .el-table__body-wrapper::-webkit-scrollbar {
  width: 7px;
}
.el-table /deep/ .el-table__body-wrapper::-webkit-scrollbar-thumb {
  background: #77777751;
  border-radius: 4px;
}
/** 表格 透明⬆⬆⬆*/
</style>
